.seo-editor {
  display: flex;
  justify-content: space-between;
}

.seo-editor nav {
  width: 40px;
  padding-top: 10px;
}

.seo-editor nav ul {
  list-style: none;
  list-style-type: none;
  padding: 0;
  margin: 0;
}

.seo-editor nav li {
  margin-bottom: 4px;
}

.seo-editor nav a {
  display: block;
  width: 40px;
  height: 40px;
  line-height: 40px;
  text-align: center;
  background: white;
  border: 1px solid #aaaaaa;
  border-right: 0;
  border-radius: 4px 0 0 4px;
}

.seo-editor .seo-tab-container {
  flex-grow: 1;
  border: 1px solid #aaaaaa;
  background: white;
  border-radius: 4px;
  padding: 20px;
}

.seo-editor .seo-tab {
  display: none;
}

.seo-editor .seo-tab.active {
  display: block;
}

.seo-editor .seo-input {
  margin-bottom: 20px;
}

.seo-editor .seo-section .fields {
  max-width: 500px;
}

@media (min-width: 1200px) {
  .seo-editor .seo-section {
    display: flex;
    justify-content: flex-start;
  }
}

.seo-editor .preview-holder {
  padding: 20px 0;
  font-family: Arial,Helvetica,sans-serif;
  font-style: normal;
}

.seo-editor .preview-holder .preview-card {
  padding: 10px;
  border-bottom: 1px hidden #fff;
  border-radius: 2px;
  box-shadow: 0 1px 2px rgba(0, 0, 0, 0.2);
}

.seo-editor .preview-holder h3 {
  color: #1e0fbe;
  font-size: 16px;
  line-height: 20px;
  margin: 0;
  font-weight: 400;
}

.seo-editor .preview-holder .preview-link {
  color: #006621;
  font-style: normal;
  margin: 0 0 9px;
  font-size: 14px;
  line-height: 20px;
  border-bottom: 1px solid #ebebeb;
  padding-bottom: 9px;
}

.seo-editor .preview-holder .preview-description {
  font-size: 14px;
  line-height: 20px;
}

.seo-editor .preview-holder .preview-card.facebook {
  max-width: 476px;
  width: 100%;
  box-shadow: 0 0 0 1px rgba(0, 0, 0, 0.15) inset, 0 1px 4px rgba(0, 0, 0, 0.1);
  position: relative;
  font-family: SF Optimized, system-ui, -apple-system, BlinkMacSystemFont, '.SFNSText-Regular', sans-serif;
  font-size: 12px;
  line-height: 16px;
}

.seo-editor .preview-holder .preview-card.facebook h3 {
  font-family: Georgia, serif;
  font-size: 18px;
  font-weight: 500;
  line-height: 22px;
  margin-bottom: 4px;
  max-height: 110px;
  overflow: hidden;
  word-wrap: break-word;
  color: black;
}

.seo-editor .preview-holder .preview-card.facebook .preview-link {
  border: 0;
  font-size: 11px;
  line-height: 11px;
  text-transform: uppercase;
  color: #90949c;
  margin: 0;
  padding: 0;
}

.seo-editor .preview-holder .preview-card.twitter {
  max-width: 506px;
  width: 100%;
  border-radius: .42857em;
  border-width: 1px;
  border-style: solid;
  border-color: #E1E8ED;
  box-sizing: border-box;
  color: inherit !important;
  overflow: hidden;
}

.seo-editor .preview-holder .preview-card.twitter .preview-contents {
  padding: 10px;
  font-family: "Helvetica Neue",Helvetica,Arial,sans-serif;
  line-height: 1.3em;
}

.seo-editor .preview-holder .preview-card.twitter h3 {
  font-size: 14px;
  color: black;
  font-weight: bold;
}

.seo-editor .preview-holder .preview-card.twitter .preview-description {
  margin: 0;
}

.seo-editor .preview-holder .preview-card.twitter .preview-link {
  margin: 0;
  padding: 0;
  color: #8899A6;
  border: 0;
}




